import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  Div,
  Radio,
  Button,
} from "@vkontakte/vkui";

const ModalADS = ({
  setActiveModal,
  APP_AVATAR,
  nativeAds
}) => {

  return (

    <ModalRoot activeModal="ads">
      <ModalCard
        id="ads"
        onClose={() => setActiveModal(null)}
        icon={<Avatar size={72} src={APP_AVATAR} />}
        header="Получение бесплатной слежки"
        caption={
          <>
            <Div
              style={{
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
                width: "100%",
                padding: "20px 0px 20px 0px",
              }}
            >
              <div>Чтобы получить одну бесплатную слежку, необходимо 10 раз </div>
              <div>посмотреть рекламный ролик.</div>
              <div>После чего, вы автоматически получите бесплатную слежку.</div>
              <div>В день можно получить максимум 5 слежек.</div>
            </Div>
            <Div>
              <Button
                style={{
                  backgroundColor: "#184a6e",
                  color: "white",
                  borderRadius: "13px",
                }}
                size="xl"
                level="2"

                onClick={nativeAds}
              >
                Смотреть рекламу
              </Button>
            </Div>
          </>
        }
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalADS };
