import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  Div,
  Radio,
  Button,
} from "@vkontakte/vkui";

import { APIManager, sharing } from "../helpers";
const { callMethod } = APIManager;

const ModalGuard = ({
  setActiveModal,
  onChangeHandlerAmount,
  APP_AVATAR,
  getDonateLink,
  getUser,
  type,
}) => {
  const [sum, setSum] = useState(49);
  const [link, setLink] = useState("");

  useEffect(() => {
    callMethod("getDonateLink", {
      amount: type === "guard" ? 199 : sum,
    }).then(({ status, message }) => {
      // console.log(message);
      if (status === "success") {
        setLink(message);
        getUser();
      } else {
        // openAlert(message, "red");
        console.log(message);
      }
    });
  }, [sum]);

  return (
    <>

      <ModalRoot activeModal="guard">
        <ModalCard
          id="guard"
          onClose={() => setActiveModal(null)}
          icon={<Avatar size={72} src={APP_AVATAR} />}
          header="Покупка защиты от слежки"
          caption={
            <>
              <Div
                style={{
                  display: "flex",
                  alignItems: "start",
                  justifyContent: "center",
                  flexDirection: "column",
                  width: "100%",
                  padding: "20px 0px 20px 0px",
                }}
              >
                После покупки "Защиты от слежки" другие пользователи не смогут
                узнать о Вас информацию.
                </Div>
              <Div>
                <Button
                  style={{
                    backgroundColor: "#184a6e",
                    color: "white",
                    borderRadius: "13px",
                  }}
                  size="xl"
                  level="2"
                  href={link}
                  target="_blank"
                  onClick={() => setActiveModal(null)}
                >
                  Купить
                  </Button>
              </Div>
            </>
          }
        ></ModalCard>
      </ModalRoot>
    </>
  );
};

export { ModalGuard };
